#data{
    width: auto;
    margin: auto;
    display: block;
}

@media  (min-width: 900px){
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        display: block;
        background-image: url('../Assets/nav.gif');
    }
    
    
    #nav-menu .nav-list{
      justify-content: right;
      padding: 20px;
      
    }
    
    .items{
        gap: 20px;
    }
    .bread{
      color: goldenrod;
      font-size: 24px;
      font-weight: 600;
    }
}

@media  (min-width: 599px)  and (max-width: 899px) {
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        display: block;
        background-image: url('../Assets/nav.gif');
    }
    
    
    #nav-menu .nav-list{
      justify-content: center;
      padding: 5px;
      margin: auto;
    }
    
    .items{
        gap: 15px;
    }

    .bread{
      color: goldenrod;
      font-size: 18px;
      font-weight: 500;
      margin: auto;
    }
}
@media (max-width: 598px) {
    #nav-menu{
        position: sticky;
        top: 0;
        z-index: 100;    
        width: 100%;
        margin: auto;
        display: block;
        background-image: url('../Assets/nav.gif');
    }
    
    
    #nav-menu .nav-list{
      justify-content: center;
      margin: auto;
    }
    
    .items{
        gap: 12px;
    }

    .bread{
      color: goldenrod;
      font-size: 14px;
      font-weight: 300;
      margin: auto;
    }
}