#home{
    display: flex;
    border: 3px solid green;
    margin: auto;
    padding: auto;
    width: 100%;
    
}
.intro{
    width: 50%;
    padding: 5%;
    justify-content: center;
    color: whitesmoke;
}
.intro p{
    font-size: 20px;
    font-weight: 500;
}
.imgCont{
    width: 50%;
    justify-content: center;
    margin: auto;
    border: 2px solid green;
    padding: 2%;
}
.nezu{
    margin: auto;
    width: 90%;
    height: auto;
    justify-content: center;
    border: 2px solid red;
}

@media (min-width:501px) and  (max-width: 1000px){
    #home {
        display: block;
        justify-content: center;
    }
    .intro{
        width: 100%;
        justify-content: center;
        padding: auto;
    }
    .imgCont{
        width: 100%;
        justify-content: center;
        margin: auto;
        border: 2px solid green;
        padding: auto;
    }
    .intro .name{
        font-size: 58px;
    }
    .intro .description{
        font-size: 30px;
    }
    .nezu{
        margin: auto;
        width: 95%;
        height: auto;
        justify-content: center;
        border: 2px solid red;
    }


}
@media  (max-width: 500px){
    #home {
        display: block;
        justify-content: center;
    }
    .intro{
        width: 100%;
        justify-content: center;
        padding: auto;
    }
    .imgCont{
        width: 100%;
        justify-content: center;
        margin: auto;
        border: 2px solid green;
        padding: auto;
    }
    .intro .name{
        font-size: 44px;
    }
    .intro .description{
        font-size: 24px;
    }
    .nezu{
        margin: auto;
        width: 95%;
        height: auto;
        justify-content: center;
        border: 2px solid red;
    }
}